.footer {
	position: absolute;
	right: 5rem;
	bottom: 5rem;

	ul {
		display: flex;
		align-items: center;

		li {
			display: inline-block;
			vertical-align: middle;
			opacity: 0.5;
			pointer-events: auto;
			margin: 0 0.5rem;
			transition: opacity 0.5s ease;

			&.off {
				opacity: 0.1;
			}

			svg {
				width: 1.6rem;
				fill: black;

			}

			&:hover {
				opacity: 1;
			}
		}
	}

}

.is-project + .footer ul li svg {
	fill: white;
}